 /* 3rd effect css */
 .container{
    width: 100%;
    max-width: 480px;
    margin: auto;
  }
  .accordion{
    width: 100%;
    max-width: 1200px;
    margin: 3px auto;
    color: #333;
  }
  .accordionHeading{
    background: #ECECEC 0% 0% no-repeat padding-box;
    border: 1px solid #FFFFFF;
    opacity: 1;
    padding: 14px 0;
  }
  .accordionHeading .container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  }
  .accordionHeading p{
    letter-spacing: 1.2px;
    font-weight: 600;
  }
  .accordionHeading span{
    cursor: pointer;
  }
  .accordionContent{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 15px #00000029;
   
    height: 0;
    opacity: 0;
    display: flex;
    overflow: hidden;
    
    transition: all 600ms ease-in-out;
  }
  .accordionContent.show{
  
    height: 180px;
    opacity: 1;
   
  }
  .accordionContent p{
    padding-top: .5rem;
    font-size: 15px;
    line-height: 1.6;
  }
