.main_container{
    overflow-x: hidden;
}
.footer_background{
    background: #1E2224 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
    width: 100%;
}
.footer_left1 img{
    display: inline-block;
    width:3rem ;
    height: 3rem;
    margin-right: 1rem;
}
.footer_left1 p{
    color: #9D9E9E !important;
    margin-top:1rem;
    display: inline-block;
font-size: .8rem;
padding-top: .5rem;
}
.footer_left1,
.footer_left2,
.footer_center,
.footer_right1,
.footer_right2{
    margin-top: 2rem;
}
.footer_left1 h3,
.footer_left2 h3,
.footer_center h3,
.footer_right1 h3,
.footer_right2 h3{
    font-size: 1.2rem;
    color: #FFFFFF;
}
.footer_left2 p,
.footer_center p,
.footer_right1 p,
.footer_right2 p{
    font-size: 1rem;
    color: #9D9E9E !important;
    text-decoration: none;
}
.footer_right2 h1{
    color: #FFFFFF;
    font-size: 1.8rem;  
}
.footer_left2_headings{
    font-size: 1rem;
    color: #707070 !important;
    text-decoration: none;
}
.circile_media{
    display: inline-block;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    background-color: #333;


}
.footer_circle_icons{
    color: #fff;
    margin: .7rem;
    height: 1.2rem;
    width: 1.2rem;
}
.footer_circle_icons2{
    color: #fff;
    margin: .5rem;
    height: 1.2rem;
    width: 1.2rem;
}
.footer_circle_icons:hover{
    color: #144d79;
    margin: .7rem;
    height: 1.4rem;
    width: 1.4rem;
}
/* second footer css images */

.all_policy h3{
    padding-top: 1rem;
    font-size: 1rem;
    color: #fff;
text-align: center;
}
.line2{
    color: #fff;
    background: #fff;
}
/* form section here */
.email_input{
    position: relative;
    margin-top: 1rem;
}
.emailstyle{
    position: absolute;
    bottom: 0;
    right: 0;
    background: #4AE870 0% 0% no-repeat padding-box;
opacity: 1;
    width: 4rem;
    height: 2.35rem;
    border-radius: 0 3px 3px 0;
}
.circle_bg{
    margin:.1rem 0 0 1rem;
    background: #fff;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    display: inline-block;
}
.mail_circle{
    /* width: .8rem;
    height: .8rem; */
    font-size: .6rem;
    text-align: center;
    margin: .25rem;
    color: #4AE870;
}
@media screen and (max-width:800px){
    .footer_left1 img{
        display: inline-block;
        width:3rem ;
        height: 3rem;
        margin-right: 1rem;
    }
    .footer_left1 p{
        color: #9D9E9E !important;
        margin-top:1rem;
        display: inline-block;
    font-size: .8rem;
    padding-top: .5rem;
    }
        .footer_left2,
.footer_center,
.footer_right1,
.footer_right2{
    margin-top: 3rem;
} 
.footer_left2 h3,
.footer_center h3,
.footer_right1 h3,
.footer_right2 h3{
    font-size: 1rem;
  
}
.footer_left2 p,
.footer_center p,
.footer_right1 p,
.footer_right2 p{
    font-size: .8rem;
}
}